import React from 'react';
import logo from './logo1.png';
import "./error.css";
import { NavLink } from 'react-router-dom';

const Home = () => {
    return (
        <>
        <div className="background">
            <div className="container col-xxl-8 px-4 py-5">
                <div className="row flex-lg-row-reverse align-items-center g-5 py-5">
                    <div className="col-10 col-sm-8 col-lg-6">
                        <img src={logo} className="d-block mx-lg-auto img-fluid" alt="Bootstrap Themes" width="700" height="500" loading="lazy" />
                    </div>
                    <div className="col-lg-6">
                        <h1 className="display-4 fw-bold lh-1 mb-3">Build fast, responsive sites with Arsenic</h1>
                        <p className="lead">Quickly design and customize responsive mobile-first sites with Arsenic, the world’s most popular front-end open source toolkit, featuring Sass variables and mixins, responsive grid system, extensive prebuilt components, and powerful JavaScript plugins.</p>
                        <div className="d-grid gap-2 d-md-flex justify-content-md-start">
                            <NavLink className="btn btn-lg btn-primary px-4 me-md-2 btnstart" to="/doc" role="button">Get Started</NavLink>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </>


    );
}

export default Home;